.page-hero {
  padding-top: 153px;
  background-color: var(--color-athens);

  padding-bottom: 80px;
  min-height: 600px;

  background-position: right -10px top;
  background-repeat: no-repeat;

  &__title {
    margin-bottom: 23px;

    max-width: 600px;
    font-weight: 700;
    font-size: 72px;
    line-height: 130%;
    letter-spacing: 1px;
    text-transform: uppercase;

    color: var(--color-ebony);
  }

  &__descr {
    max-width: 600px;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;

    color: var(--color-storm);
  }

  &--services {
    background-image: url("../img/bg-image-services.png");
  }

  &--services-inner {
    background-image: url("../img/services-v2/bg-hero-services-v2.png");
  }

  &--work {
    background-image: url("../img/work/hero-work-bg.png");
  }
}
