.mission {
  background-color: var(--color-light);

  &__center {
    position: absolute;
    bottom: -25px;
    left: 0;
    right: 0;
    text-align: center;
    z-index: 10;
  }

  &__list {
    --mission-gap: 94px;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--mission-gap) * 2);

    margin-bottom: 27px;

    &--benefits {
      padding-bottom: 126px;
    }
  }

  &__item {
    position: relative;

    padding-top: 72px;

    background-position: top center;
    background-repeat: no-repeat;
    background-size: 48px 48px;

    text-align: center;

    &:not(:last-child) {
      &::after {
        content: "";

        position: absolute;
        top: 0;
        right: calc(-1 * var(--mission-gap));
        width: 1px;
        height: 100%;

        background-image: url("../img/divider.svg");
      }
    }

    &--quality {
      background-image: url("../img/quality.svg");
    }

    &--safety {
      background-image: url("../img/safety.svg");
    }

    &--comfort {
      background-image: url("../img/comfort.svg");
    }

    // For benefits section
    &--fixes {
      background-image: url("../img/services-v2/fixed.svg");
    }

    &--qualified {
      background-image: url("../img/services-v2/qualifed.svg");
    }

    &--control {
      background-image: url("../img/services-v2/control.svg");
    }
  }

  &__subtitle {
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;

    color: var(--color-ebony);

    margin-bottom: 10px;
  }

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;

    color: var(--color-storm);
  }

  &--benefits {
    position: relative;
    background-color: var(--color-ebony);
    padding-top: 81px;

    .title {
      color: var(--color-light);
    }

    .mission__subtitle {
      color: var(--color-light);
    }
  }
}

.know-form {
  position: relative;
  top: 92px;

  border-radius: 4px;
  padding: 48px 60px;
  background: var(--color-light);

  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 4px 4px -4px rgba(30, 33, 44, 0.03);

  &__title {
    text-align: center;

    font-weight: 700;
    font-size: 28px;
    line-height: 150%;

    margin-bottom: 32px;

    color: var(--color-ebony);
  }

  &__content {
    display: flex;
    align-items: flex-end;
  }

  &__field {
    &:not(:last-child) {
      margin-right: 24px;
    }
  }

  &__btn {
    padding: 11px 29px;

    &--benefits {
      padding: 14px 112px;
    }
  }
}
