.testimonials {
  background-color: var(--color-athens);
  padding-top: 97px;
  margin-bottom: -63px;

  &--work {
    padding-top: 81px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__center {
    align-items: center;
  }

  &__content {
    max-width: 612px;
  }

  &__title {
    margin-bottom: 60px;
  }

  &__items {
    position: relative;
    margin: auto !important;
    padding: 0 0 10px 0 !important;
  }

  &__slider-nav {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  &__image {
    position: relative;
    border-radius: 4px;
    z-index: 10;

    img {
      display: block;
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }
}

.testimonials-item {
  overflow: hidden;
  &__image {
    overflow: hidden;
    border-radius: 100%;

    width: 72px;
    height: 72px;

    background-color: var(--color-ebony);

    margin-bottom: 25px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;

    color: var(--color-gray);

    margin-bottom: 25px;
  }

  &__author {
    display: block;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;

    color: var(--color-ebony);

    margin-bottom: 5px;

    &-positions {
      display: block;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;

      color: var(--color-storm);
    }
  }
}
