.facts{
	overflow: hidden;
	padding-top: 181px;
	background-color: var(--color-light);
	position: relative;
	padding-bottom: 180px;

	&::before{
		content: "";
		position: absolute;
		left: 16%;
		top: -197px;

		width: 570px;
		height: 1065px;

		background-image: url("../img/bg-image-section-facts.png");
		background-position: center;
		// background-size: cover;
		background-repeat: no-repeat;
	}

	&__title{
		margin-bottom: 60px;
	}

	&__list{
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		margin: 0 auto;
		gap: 48px;
		max-width: 736px;
	}
}

.facts-element{
	&__circle{
		--progress-stroke: #000;
		--progress-bg-opacity: 0.3;


		width: 148px;
		height: 148px;
		position: relative;

		margin-bottom: 20px;

		svg{
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
		}

		.progress{
			stroke: var(--progress-stroke);
			transform: rotate(-90deg) translateX(20px);
			transform-origin: center;
		}

		.progress-bg{
			stroke: var(--progress-stroke);
			opacity: (var(--progress-bg-opacity));
		}		
	}

	&--one-color{
		--progress-stroke: #F89828;	
	}

	&--two-color{
		--progress-stroke: #F52F6E;
	}

	&--three-color{
		--progress-stroke: #5A87FC;
	}

	&--four-color{
		--progress-stroke: #03CEA4;
	}
	
	&__value{
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		font-weight: 700;
		font-size: 32px;
		line-height: 150%;

		color: var(--color-ebony);

	}

	&__text{
		display: block;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		text-align: center;
		color: var(--color-ebony);
	}
}