.portfolio {
  background-color: var(--color-light);
  padding-bottom: 100px;
}

.portfolio-tabs-nav {
  --gap: 20px;
  @include flex-v-center;

  transform: translateY(-59px);
  margin-bottom: 61px;

  &__item {
    width: calc((100% - (var(--gap) * 4)) / 5);

    &:not(:last-child) {
      margin-right: var(--gap);
    }
  }

  &__icon {
    display: block;
    width: 48px;
    height: 48px;
    object-fit: cover;
    margin-bottom: 16px;
    fill: var(--color-primary);
    transition: fill 0.3s ease-in-out;
    pointer-events: none;
  }

  &__btn {
    @include flex-collumn-centr;
    justify-content: center;
    border-radius: 4px;
    width: 100%;
    min-height: 154px;

    background-color: var(--color-light);

    box-shadow: var(--main-shadow);
    padding: 15px;

    transition: background 0.3s ease-in-out, box-shadow 0.3s ease-in-out;

    &--active {
      background-color: var(--color-primary);
      box-shadow: 0px 60px 80px -20px rgba(255, 90, 48, 0.16),
        0px 26px 24px -10px rgba(255, 90, 48, 0.1),
        0px 12px 10px -6px rgba(255, 90, 48, 0.08),
        0px 4px 4px -4px rgba(30, 33, 44, 0.05);
      pointer-events: none;

      .portfolio-tabs-nav__icon {
        fill: var(--color-light);
      }

      .portfolio-tabs-nav__text {
        color: var(--color-light);
      }
    }
  }

  &__text {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;

    color: var(--color-manate);
    transition: color 0.3s ease-in-out;
    pointer-events: none;
  }
}

.portfolio-tabs {
  padding-bottom: 61px;

  &__item {
    grid-column: 4 span;
    display: none;

    &--visible {
      display: block;
    }

    &--visible-more {
      display: block !important;
    }
  }
}

.load-more {
  &__btn {
    display: inline-flex;
    align-items: center;

    font-weight: 700;
    font-size: 16px;

    color: var(--color-gray);

    &--show {
      display: none;
    }
  }

  &__icon {
    width: 24px;
    height: 24px;
    object-fit: cover;
    color: inherit;
    fill: currentColor;
    margin-right: 17px;
  }
}
