.how-we-do {
  padding-bottom: 120px;

  &__title {
    font-weight: 700;
    font-size: 46px;
    line-height: 130%;

    margin-bottom: 59px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    max-width: 285px;

    &:not(:last-child) {
      margin-right: 15px;
    }
  }

  &__number {
    display: block;
    position: relative;

    font-weight: 700;
    font-size: 46px;
    line-height: 130%;
    color: #d7dadd;

    margin-bottom: 19px;

    &::after {
      content: "";
      position: absolute;
      display: block;
      top: 27px;
      right: 0;
      max-width: 209px;
      width: 100%;
      border-bottom-style: dashed;
      border-width: 2px;
      border-color: currentColor;
    }
  }

  &__subtitle {
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;

    margin-bottom: 10px;
  }

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;

    color: var(--color-storm);
  }
}
