.breadcrumbs {
	@include flex-v-center;
	margin-bottom: 40px;

	&__item {
		position: relative;

		&:not(:last-child) {
			margin-right: 21px;

			&::after {
				content: "/";
				position: absolute;
				top: 0;
				right: -14px;

				color: #9A9CA5;
			}
		}

		font-size: 14px;
		line-height: 150%;

		color: var(--color-gray);
	}

	&__link {
		color: inherit;
		font-size: inherit;
		font-weight: inherit;

		&:not([href]) {
			color: #9A9CA5;
		}
	}

}