.partners {
  background-color: var(--color-athens);
  padding-top: 80px;
  padding-bottom: 80px;

  &--services {
    padding-top: 0;
    padding-bottom: 117px;
  }

  &--work {
    background-color: var(--color-light);
    padding-top: 183px;
    padding-bottom: 114px;
  }

  &__title {
    margin-bottom: 59px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 102px;
  }
}
